.btn-filter {
  border-radius: 20.5px;
  width: 180px;
  height: 48px;
  padding: 1%;
  background: var(--grey);
  margin: 0.5%;
  border: none;
}

.btn-filter:hover {
  background: var(--lightblue);
  transform: translateY(-2px);
  box-shadow: 8px 8px 18px 0px rgb(50 50 50 / 75%);
}

.text-full {
  width: 100%;
  height: 130%;
}

.text-full:hover {
  color: var(--white);
}

.text-white {
  color: white;
}

.pink {
  background: rgb(216, 71, 156);
}

.pink:hover {
  background: rgb(216, 6, 129);
}

@media (max-width: 720px) {
  .btn-filter {
    width: 80%;
  }
}
